<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">EDIT MANAGEMENT FEES</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">TENANT</label>
            <div class="col-md-3">
              <vue-single-select
                v-model="management.tenant"
                :options="this.tenant"
                :value="management.tenant"
              ></vue-single-select>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">BLOCK NO</label>
            <div class="col-md-3">
              <vue-single-select
                v-model="management.blockno"
                :options="this.blockno"
                :value="management.blockno"
              ></vue-single-select>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">FEES</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="management.fees" :value="management.fees"></fg-input>
            </div>
          </div>
          <!--Date Picker-->
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">START DATE</label>
            <div class="col-md-2">
              <fg-input>
                <el-date-picker
                  v-model="management.startDate"
                  type="date"
                  placeholder="Pick a day"
                  :picker-options="pickerOptions"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </fg-input>
            </div>
            <label class="col-form-label">END DATE</label>
            <div class="col-md-2">
              <fg-input>
                <el-date-picker
                  v-model="management.endDate"
                  type="date"
                  placeholder="Pick a day"
                  :picker-options="pickerOptions"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">STATUS</label>
            <div class="col-md-3">
              <n-radio
                class="col-md-1 display-inline"
                v-model="enabledRadio"
                label="PENDING"
              >PENDING</n-radio>
              <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="PAID">PAID</n-radio>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-4">
              <n-button
                class="button"
                type="info"
                @click.native="radio_status(enabledRadio); put(); handleUpdate();"
              >Update</n-button>
              <a class="cancel">Cancel</a>
              <!-- <n-button class="button" type="danger">Cancel</n-button> -->
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>

<script>
import { DatePicker } from "element-ui";
import { Radio } from "src/components";
import swal from "sweetalert2";

export default {
  components: {
    [DatePicker.name]: DatePicker,
    [Radio.name]: Radio
  },
  data() {
    return {
      id: this.$route.params.id,
      management: {
        tenant: "",
        blockno: "",
        fees: "",
        startDate: "",
        endDate: "",
        status: ""
      },
      enabledRadio: "PAID",
      pickerOptions: {
        shortcuts: [
          {
            text: "Today",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Yesterday",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "A week ago",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      tenant: [],
      block: [],
      key_data: {
        tenant: "",
        blockno: "",
        fees: "",
        startDate: "",
        endDate: "",
        status: ""
      }
    };
  },
  methods: {
    //update the data to database
    put() {
      this.$http.put('https://admindashboard-b2ab9.firebaseio.com/management/'+ this.id +'.json',this.management)
    },
    radio_status(er){
      if (er == "PAID"){
        this.management.status = 'PAID'; 
      }
      else{
        this.management.status = 'PENDING';
      }
    },
    handleUpdate() {
      swal({
        title: `You updated ${this.management.tenant}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "btn btn-success btn-fill"
      });
    }
  },
  created(){
    //get data to show in textboxes
    this.$http.get('https://admindashboard-b2ab9.firebaseio.com/management.json').then(function(data){
      return data.json();
    }).then(function(data){
      //pass data to key_data object
      this.key_data = data
      //set the fields accordingly with the unique id
      this.management.tenant = this.key_data[this.id].tenant
      this.management.fees = this.key_data[this.id].fees
      this.management.blockno = this.key_data[this.id].blockno
      this.management.status = this.key_data[this.id].status
      this.management.startDate = this.key_data[this.id].startDate
      this.management.endDate = this.key_data[this.id].endDate

      //get tenant name from tenant table
      this.$http.get('https://admindashboard-b2ab9.firebaseio.com/tenant.json').then(function(data){
        return data.json(); //return promise
      }).then(function(data){
        var tenant_Array = [];
          for (let key in data){
            tenant_Array.push(data[key].name);
          }
        this.tenant = tenant_Array;

        //get blockno from block table
        this.$http.get('https://admindashboard-b2ab9.firebaseio.com/block.json').then(function(data){
          return data.json();
        }).then(function(data){
            var block_Array = [];
            for (let key in data){
              block_Array.push(data[key].blockno);
            }
          this.block = block_Array;
          })
          //toggle radio button
        if ( this.management.status == 'PAID'){
          this.enabledRadio = 'PAID'
        }else{
          this.enabledRadio = 'PENDING'
        }
      })
    })
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel {
  /* display: inline; */
  margin-left: 20px;
  color: red;
  text-decoration: underline !important;
}
</style>
